<template>
     <div class="container">
         <div v-if="curentUser">
             <div v-if="isUserExist($route.params.contactName)" class="contact-card">
                <div class="contact-card__name">{{ $route.params.contactName }}</div>
                <div class="contact-card__image">{{curentUser.status}}</div>
                <div class="contact-card__bottom">
                    <div v-if="authorizedUser" class="contact-card__start-chat">
                        <span @click="checkChat($route.params.contactName)">Start chat</span>
                    </div>
                </div>
            </div>
            <NotFound404 v-else></NotFound404>
         </div>
        <div v-else>Loading...</div>  
    </div>
</template>

<script>
    import Axios from 'axios'
    
    import NotFound404 from '../components/NotFound404'
    import {mapState, mapActions} from 'vuex'

    export default {
        name: "contactView",
        components:{
            NotFound404
        },
        computed:{
            ...mapState([
                'contacts',
                'chats',
                'authorizedUser'
            ]),
            curentUser: function(){
                let routePathName = this.$route.params.contactName
                return this.contacts.filter(contact => contact.name == routePathName)[0]
            } 
        },
        methods:{
            ...mapActions([
                'GET_CONTACT',
                'SET_NEW_CHAT',
                'CHECK_AUTORIZED'
            ]),
            isUserExist(user){
                let result = this.contacts.filter(contact => contact.name == user).length
                return result==1
            },
            isChatExist(user){
                let arrWeNeed = [user, this.authorizedUser]
                let result = 0

                this.chats.forEach(chat => {
                    if(_.difference(chat.users, arrWeNeed).length == 0){
                        result++
                    }
                });

                return result==1
            },
            // redirect to chat
            redirectToChat(chatName){
                this.$router.push('/'+chatName+'/chat')
            },
            // create chate
            createChat(chatID){
                let firsUser = this.curentUser.name
                let secondUser = this.authorizedUser

                const newChat = {
                    "id": chatID,
                    "users": [firsUser, secondUser],
                    "messages": []
                }

                this.SET_NEW_CHAT(newChat)                   
            },
            checkChat(chatName){ 
                if(!this.isChatExist(chatName)){
                    let chatID = this.chats.length + 1

                    this.createChat(chatID)
                    this.redirectToChat(chatName)
                }else{
                    this.redirectToChat(chatName)
                }
            }
        },
        mounted(){
            if(this.contacts.length == 0){
                this.GET_CONTACT()
            }
            // autorized
            if(this.authorizedUser == ''){
            this.CHECK_AUTORIZED()
            }
        }
    }
</script>


<style lang="scss" scoped>
    .contact-card {
        display: flex;
        flex-flow: column;
        align-items: center;

        &__name {
            margin: 30px 0 30px 0;
            font-size: 35px;
        }

        &__image {
            width: 250px;
            height: 320px;

            background-color: rgb(143, 143, 143);
        }

        &__bottom {
            display: flex;
            justify-content: flex-end;

            margin: 30px 0 50px 0;
        }

            &__start-chat {
                span{
                    padding: 10px 15px;
                    
                    border-radius: 5px;

                    background-color: rgb(0, 162, 255);
                    color: #fff;

                    cursor: pointer;
                }
            }
    }

    .header{
        position: relative;
    }
    .back-button{
        position: absolute;
        left: 30px;
    }
</style>