<template>
  <div class="container">
     <main>
        <template v-for="contact in contacts" :key="contact.name">
          <div class="contacts" v-if="contact.name != authorizedUser">
            <div class="contacts__item">
                <div class="contacts__image"></div>
                <div class="contacts__name"><router-link :to="{name: 'contactView', params: {contactName: contact.name, status: contact.status}}">{{contact.name}}</router-link></div>
                <div class="contacts__status" :class="{contacts__statusOnline: contact.status}"></div>
            </div>
          </div>
        </template>
    </main>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapActions, mapState} from 'vuex'

export default {
  name: 'Home',
  computed: {
    ...mapState([
      'contacts',
      'authorizedUser'
    ])
  },
  methods:{
    ...mapActions([
      'GET_CONTACT',
      'CHECK_AUTORIZED'
    ])
  },
  mounted(){
    this.GET_CONTACT()
    // autorized
    if(this.authorizedUser == ''){
      this.CHECK_AUTORIZED()
    }
  }
}
</script>
<style lang="scss" scoped>
    main {
    min-height: calc(100vh - 160px)
  }

  .contacts {
    margin-top: 30px;
    margin-bottom: 30px
  }

  .contacts__item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: center;
    align-items: center
  }

  .contacts__image {
    margin-top: 25px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background-color: rgba(128, 128, 128, .322)
  }

  .contacts__name {
    cursor: pointer
  }

  .contacts__status {
    border-radius: 50%;
    background-color: red;
    height: 10px;
    width: 10px
  }

  .contacts__statusOnline {
    background-color: green
  }
</style>