import Axios from 'axios'
import { createStore } from 'vuex'

export default createStore({
  state: {
    authorizedUser: '',
    contacts: [],
    chats: []
  },
  
  mutations: {
    CONTACT_TO_STORE(state, contacts){
      state.contacts = contacts;
    },
    CHATS_TO_STORE(state, chats){
      state.chats = chats;
    },
    SET_NEW_MESSAGE(state, {updatedChat, chatId}){
      let chat = state.chats.filter(chat => chat.id == chatId)[0]
      chat = updatedChat
    },
    NEW_CHAT_TO_STORE(state, newChat){
      state.chats.push(newChat)
    },
    NEW_CONTACT_TO_STORE(state, newContact){
      state.contacts.push(newContact)
    },
    LOG_OUT(state){
      state.authorizedUser = ''
    },
    LOG_IN(state, authorizedUser){
      state.authorizedUser = authorizedUser
    }
  },

  actions: {
    GET_CONTACT({commit}){
      return Axios.get('http://localhost:3000/contacts')
        .then(contacts =>{
          commit('CONTACT_TO_STORE', contacts.data)
        })
    },
    GET_CHATS({commit}){
      return Axios.get('http://localhost:3000/chats')
        .then(chats =>{
          commit('CHATS_TO_STORE', chats.data)
        })
    },
    SEND_MESSAGE({commit}, {updatedChat, chatId}){
      commit('SET_NEW_MESSAGE', {updatedChat, chatId})
      Axios.put(`http://localhost:3000/chats/${chatId}`, updatedChat)
      .then((respons) => {
        return respons;
      })
    },
    SET_NEW_CHAT({commit}, newChat){
      commit('NEW_CHAT_TO_STORE', newChat)
      Axios.post(`http://localhost:3000/chats`, newChat)
      .then((respons) => {
        return respons;
      })
    },
    // registration
    SET_NEW_CONTACT({commit}, newContact){
      commit('NEW_CONTACT_TO_STORE', newContact)
      Axios.post(`http://localhost:3000/contacts`, newContact)
      .then((respons) => {
        return respons;
      })
    },
    // logIn
    LOG_IN({commit}, authorizedUser){
      commit('LOG_IN', authorizedUser.name)
      Axios.put(`http://localhost:3000/authorizedUser`, authorizedUser)
      .then((respons) => {
        return respons;
      })
    },
    CHECK_AUTORIZED({commit}){
      let authorizedUser = ''

      Axios.get(`http://localhost:3000/authorizedUser`)
      .then((respons) => {
        authorizedUser = respons.data.name
        commit('LOG_IN', authorizedUser)
        return respons;
      })
    },
    // logOut
    LOG_OUT({commit}, deAutorized){
      commit('LOG_OUT')
      Axios.put(`http://localhost:3000/authorizedUser`, deAutorized)
      .then((respons) => {
        return respons;
      })
    }
  },

  modules: {
  }
})
