<template>
    <div class="header">
        <div 
			class="back-button" 
			:class="{back_buttonDisabled: isHomePage}" 
			@click="backInHistory">
			
		   Back
		</div>
        <nav>
            <div class="header__nav">
				<template
				    v-for="link in mainLinks" 
					:key="link.id"
				>
					<div v-if="getAutorzedUserInTemplate(link.show)" class="header__links" >

						<div 
							:class="{active_link: link.active}">

							<i :class="link.imageOfLink"></i>
							<router-link :to="link.linkPath"><span></span></router-link>
						</div>
					</div>
				</template>
				<div @click="showPopUpinAPP"  class="header__links" :class="{'header__links--authorized':authorizedUser}">
					<i class="fas fa-user"></i>
					<span style="margin-top: 50px; color: black;">{{authorizedUser}}</span>
				</div>
            </div>
        </nav>
    </div>
</template>

<script>
import router from '../router/index.js';
import {mapState} from 'vuex'
// components

export default {
    data(){
		return{
			mainLinks:[
				{id:'0', imageOfLink: "fas fa-home", active: false, linkPath: '/', show: true},
				{id:'1', imageOfLink: "fas fa-phone", active: false, linkPath: '/chats', show: 'authorizedUser'},
			],
		}
	},
	computed: {
		isHomePage(){
			return this.$route.path === "/"
		},
		...mapState([
			'authorizedUser'
		])
	},
	methods: {
		backInHistory(){	
			router.go(-1)
		},
		showPopUpinAPP(){
			this.$parent.showPopUp()
		},
		getAutorzedUserInTemplate(link){
			if(link != 'authorizedUser'){
				return true
			}else if(link == 'authorizedUser'){
				return this.authorizedUser
			}else{
				return false
			}
		}
	}
}
</script>

<style lang="scss" scoped>
.header {
	height: 80px;
	background-color: rgba(128, 128, 128, .322);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center
}

.header__nav {
	width: 400px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	justify-content: space-between;
	-ms-flex-align: center;
	align-items: center
}

.header__links {
	cursor: pointer;
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	padding: 0 10px
}
.header__links--authorized{
	color: rgb(231, 73, 73);
}
.header__links>a>span{
	width: 100%;
	height: 100%;
}
.active_link{
	color: rgb(20, 157, 248);
}


.header__links * {
	position: absolute;
	min-width: 25px;
	min-height: 25px;
	max-width: 25px;
	max-height: 25px
}

.header__links i {
	color: #494949;
	font-size: 24px
}

    .back-button{
	position: absolute;
	cursor: pointer;
	
	left: 30px;
}
.back_buttonDisabled{
	display: none;
}
</style>