<template>
  <div class="container">
        <div v-if="curentChat">
            <div v-if="isChatExist($route.params.contactName)" class="chat">
                <h2>{{curentChat.users.filter(user => user != authorizedUser)[0]}}</h2>
                <div class="chat__messages">
                    <p  v-for="message in curentChat.messages" 
                        :key="message.from" 
                        class="chat__message" 
                        :class="{'chat__message--my': !(message.from==authorizedUser)}">

                            {{message.text}}
                            <span>{{message.timeOfSend}}</span> 
                        </p>
                </div>
                <div class="chat__form">
                    <form action="" @submit="onSubmit">
                        <textarea @keypress.enter="sendMessage" v-model="messageVal" class="chat__textarea" name="" id="" cols="52" rows="10"></textarea>
                        <button @click="sendMessage" class="chat__button" type="submit">Send</button>
                    </form>
                </div>
            </div>
            <NotFound404 v-else></NotFound404>
        </div>
        <div v-else>Loading...</div>
    </div>
</template>

<script>
import NotFound404 from '../components/NotFound404'

import {mapState, mapActions} from 'vuex'

export default {
    components:{
            NotFound404
    },
    data(){
        return{
            messageVal: '',
        }
    },
    computed:{
        ...mapState([
            'chats',
            'authorizedUser'
        ]),
        curentChat: function(){
            let routePathName = this.$route.params.contactName
            let result = {}

            this.chats.forEach(chat => {
                if(_.difference(chat.users, [routePathName, this.authorizedUser]) == 0){
                    result = chat
                }
            });

            return result
        } 
    },
    methods:{
        ...mapActions([
                'GET_CHATS',
                'SEND_MESSAGE',
                'CHECK_AUTORIZED'
        ]),
        isChatExist(user){
            let arrWeNeed = [user, this.authorizedUser]
            let result = 0

            this.chats.forEach(chat => {
                if(_.difference(chat.users, arrWeNeed).length == 0){
                    result++
                }
            });

            return result==1
        },
        onSubmit(e){
            e.preventDefault();
        },
        // send message
        sendMessage(){
            let chatId = this.curentChat.id
            // 
            let messageID = this.curentChat.messages.length + 1
            let messageFrom = this.curentChat.users.filter(user => user != this.authorizedUser)
            let messageText = this.messageVal
            let time = new Date()
            let hours = time.getHours()
            let minutes = time.getMinutes()
            let messageSendTime = `${hours}:${minutes}`

            const message = {
                "id": messageID,
                "from": messageFrom,
                "text": messageText,
                "timeOfSend": messageSendTime
            }

            this.curentChat.messages.push(message)
            let updatedChat = this.curentChat

            this.SEND_MESSAGE({updatedChat, chatId})
            .then(() => {
                this.messageVal = ''
            })
        }
    },
    mounted(){
        if(this.chats.length == 0){
            this.GET_CHATS()
        }
        // autorized
        if(this.authorizedUser == ''){
        this.CHECK_AUTORIZED()
        }
    }
}
</script>

<style lang="scss" scoped>
    .chat {
    display: flex;
    flex-flow: column;
    align-items: center;

    h2{
        font-size: 35px;
    }

    &__messages {
        width: 500px;

        padding: 5px;

        display: flex;
        flex-flow: column;

        height: 50vh;
        overflow-y: scroll;
    }
        &__message{
            max-width: 40%;

            margin-top: 25px;
            margin-bottom: 0;

            padding: 5px 10px;
            background-color: rgb(214, 214, 214);
            color: rgb(53, 53, 53);

            border-radius: 5px 5px 5px 0px;

            display: inline;
            position: relative;

            span{
                color: rgb(110, 110, 110);

                position: absolute;
                bottom: -20px;
                right: 5px;
            }
        }
        &__message--my{
            background-color: rgb(144, 190, 216);
            color: rgb(13, 60, 87);
            margin-left: 55%;

            border-radius: 5px 5px 0px 5px;
        }

    &__form {
        width: 500px;
        margin-top: 20px;

        form{
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
        &__textarea{
            border: 1px solid rgb(207, 207, 207);

            max-height: 20px;
            padding: 5px;
        }
        &__button{

            padding: 5px;
        }
}

</style>