<template>
    <div class="popUp">
        <div class="popUp__window">
            <div @click="hidePopUpInParent" class="popUp__close">
                <div></div>
                <div></div>
            </div>
            <!-- login -->
            <div class="login-logout">
                <div v-if="!authorizedUser">
                    <h2>LogIn</h2>
                    <form v-on:submit="logIn" action="" method="GET">
                        <label class="auth-label" for="name"></label><input v-model="formLog.name" class="auth-input" type="text" name="name" id="name" placeholder="Name">
                        <label class = "auth-label" for="password"></label><input v-model="formLog.password" class="auth-input" type="password" name="password" id="password" placeholder="Password">
                        <input class="auth-submit" type="submit" value="LogIn">    
                    </form>
                </div>
                <form v-else v-on:submit="logOut" action="" method="POST">
                    <input class="auth-submit auth-submit--logout" type="submit" value="LogOut">
                </form>
            </div>
            <!-- registration -->
            <div v-if="!authorizedUser" class="registration">
                <h2>Registration</h2>
                <form action="" method="POST" onsubmit="return false;" v-on:submit="sendRegistrationData">
                    <label class = "auth-label" for="regName"></label><input v-model="formReg.name" class="auth-input" type="text" name="name" id="regName" placeholder="Name">
                    <label class = "auth-label" for="regPassword"></label><input v-model="formReg.password" class="auth-input" type="password" name="password" id="regPassword" placeholder="Password">
                    <label class = "auth-label" for="regRePassword"></label><input v-model="formReg.rePassword" class="auth-input" type="password" name="rePassword" id="regRePassword" placeholder="Password again">
                    <input class="auth-submit" type="submit" value="Register">
                </form>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios/lib/axios.js'
import {mapState, mapActions} from 'vuex'

export default {
    name: "Auth",
    data(){
        return{
            formReg:{
                name: '',
                password: '',
                rePassword: '',
            },
            formLog:{
                name: '',
                password: '',
            }
        }
    },
    computed:{
        ...mapState([
            'authorizedUser'
        ])
    },
    methods:{
         ...mapActions([
            'SET_NEW_CONTACT',
            'LOG_OUT',
            'LOG_IN'
        ]),
        hidePopUpInParent(){
            this.$parent.hidePopUp()
        },
        // Auth
        sendRegistrationData(e){
            e.preventDefault()

            if(this.formReg.password == this.formReg.rePassword){
                if(this.formReg.password != '' & this.formReg.password != ''){
                    axios.get(`http://localhost:3000/contacts`)
                    .then((respons) => {
                        let checkUserExist = respons.data.filter(user => user.name == this.formReg.name)
                        if(!checkUserExist.length){
                            let newUserID = respons.data.length + 1
                            let password = this.formReg.password
                            let name = this.formReg.name

                            const newContact = {
                                "id": newUserID,
                                "password": password,
                                "name": name,
                                "photo": "0",
                                "status": false
                            }
                            this.SET_NEW_CONTACT(newContact)

                            this.formReg.name = ''
                            this.formReg.password = ''
                            this.formReg.rePassword = ''

                            this.hidePopUpInParent()
                            this.$router.push('/')
                        }else{
                            alert('User with this name already exist')
                        }
                    })
                }else{
                    alert('empty field name or password')
                }
            }else{
                alert('rePassword is wrong')
            }
        },
        logIn(e){
            e.preventDefault()

            if(this.formLog.password != '' & this.formLog.password != ''){
               axios.get(`http://localhost:3000/contacts`)
               .then((respons) => {
                   let checkUserExist = respons.data.filter(user => user.name == this.formLog.name)
                   if(checkUserExist.length == 1){
                       if(checkUserExist[0].password == this.formLog.password){
                           const authorizedUser = {
                               name: this.formLog.name
                           }
                           this.LOG_IN(authorizedUser)

                           this.formReg.name = ''
                           this.formReg.password = ''

                           this.hidePopUpInParent()
                           this.$router.push('/') 
                       }else{
                           alert('password is wrong')
                       }
                   }else{
                       alert('No such user')
                   }
               })
            }else{
                alert('empty field name or password')
            }
        },
        logOut(e){
            e.preventDefault()

            const deAutorized = {
                name: ""
            }
            this.LOG_OUT(deAutorized)

            this.hidePopUpInParent()
            this.$router.push('/')
        }
    },
}
</script>
<style lang="scss" scoped>
    .popUp{
        position: absolute;
        width: 100vw;
        height: 100vh;

        background-color: rgba(0, 0, 0, 0.404);

        display: flex;
        justify-content: center;
        align-items: center;

        z-index: 1000;

        &__close{
            position: absolute;
            top: 12px;
            right: 14px;
            cursor: pointer;
            width: 20px;
            height: 20px;

            div{
                margin-top: 10px;
                width: 100%;
                height: 1px;
                background-color: rgb(58, 58, 58);
                display: block;
                position: absolute;
            }
            div:first-child{
                transform: rotate(45deg);
            }
            div:last-child{
                transform: rotate(-45deg);
            }
        }
        &__close:hover>div{
            background-color: rgb(255, 61, 61);
        }

        &__window{
            width: 600px;

            background-color: #fff;
            border-radius: 10px;
            box-shadow: 0 0 15px 3px rgba(0, 0, 0, 0.048);

            padding: 45px 15px;

            position: relative;
        }
        form{
            display: flex;
            flex-flow: column;
            align-items: center;

            margin-top: 5px;
        }
        h2{
            text-align: center;
            margin: 0;
        }
    }
    // Auth
    .auth-label {
        
    }
    .auth-input {
        font-size: 18px;
        border: 1px solid rgb(177, 177, 177);
        width: 80%;  
        border-radius: 5px;

        margin-top: 10px;
        padding: 5px;
    }
    .auth-submit {
        cursor: pointer;
        border: none;
        border-radius: 8px;

        min-width: 150px;

        margin-top: 20px;
        padding: 8px;

        font-size: 14px;
    }

    .login-logout {
    }
    .registration {
    }
</style>